// Path: lib/data.ts

// Alamat backend Laravel (MAMP)
export const API_BASE_URL = "http://127.0.0.1:8000/api";
export const APP_BASE_URL = "http://127.0.0.1:8000";

/**
 * Fungsi untuk mengambil Cookie CSRF dari Laravel Sanctum.
 * Wajib dipanggil sebelum melakukan request POST/PUT/DELETE.
 */
export async function getCsrfToken() {
  try {
    await fetch(`${APP_BASE_URL}/sanctum/csrf-cookie`, {
      method: "GET",
      credentials: "include", // Penting agar browser menyimpan cookie session
    });
  } catch (error) {
    console.error("Gagal mengambil CSRF Token:", error);
  }
}

/**
 * Fungsi Umum untuk request GET ke API Laravel.
 */
export async function fetchFromApi<T>(endpoint: string): Promise<T | null> {
  try {
    const res = await fetch(`${API_BASE_URL}${endpoint}`, {
      cache: "no-store",
      headers: {
        "Accept": "application/json",
      },
    });

    if (!res.ok) return null;
    const json = await res.json();
    return json.success ? json.data : null;
  } catch (error) {
    console.error(`API Fetch Error pada ${endpoint}:`, error);
    return null;
  }
}

/**
 * Fungsi Umum untuk request POST ke API Laravel.
 * Digunakan untuk Login, Register, dan Tambah Data.
 */
export async function postToApi(endpoint: string, body: any) {
  try {
    // 1. Jalankan handshake CSRF
    await getCsrfToken();
    
    // 2. Ambil token dari localstorage jika ada (untuk otorisasi)
    const token = typeof window !== "undefined" ? localStorage.getItem("auth_token") : null;

    const res = await fetch(`${API_BASE_URL}${endpoint}`, {
      method: "POST",
      headers: {
        "Accept": "application/json",
        "Content-Type": "application/json",
        "Authorization": token ? `Bearer ${token}` : "",
      },
      body: JSON.stringify(body),
      credentials: "include", // Kirimkan cookie session kembali ke server
    });

    const json = await res.json();
    
    // Memberikan return object yang konsisten untuk Next.js
    return {
      status: res.status,
      success: json.success || (res.status >= 200 && res.status < 300),
      message: json.message,
      data: json.data,
      token: json.token,
      role: json.role,
      user: json.user,
      errors: json.errors // Untuk menangkap validasi error dari Laravel
    };
  } catch (error) {
    console.error(`API Post Error pada ${endpoint}:`, error);
    return { 
      success: false, 
      message: "Gagal terhubung ke server. Pastikan Backend Laravel menyala." 
    };
  }
}

/**
 * Fungsi Utama untuk memuat data Homepage secara dinamis dari Database.
 */
export async function loadAllFromDatabase() {
  try {
    // 1. Ambil data kuliner asli dari MySQL lewat API Laravel
    const res = await fetch(`${API_BASE_URL}/culinaries`, { cache: "no-store" });
    const json = await res.json();
    const dbCulinaries = json.data || [];
    
    // 2. Load data statis kategori & provinsi dari file lokal Next.js
    const [resProv, resKat] = await Promise.all([
      fetch("/mock/home/provinsi_kota.json").then(r => r.json()).catch(() => ({})),
      fetch("/mock/home/kategori.json").then(r => r.json()).catch(() => [])
    ]);

    // 3. Mapping data agar cocok dengan properti yang dibutuhkan komponen UI (KulinerCard/Grid)
    const sanitized = dbCulinaries.map((item: any) => ({
      ...item,
      id: item.id.toString(), // ID dipaksa string agar logic .includes() di UI lancar
      title: item.nama,        // UI butuh 'title', DB punya 'nama'
      rating: parseFloat(item.rating) || 0,
      images: Array.isArray(item.images) ? item.images : ["/placeholder.svg"]
    }));

    return {
      provKota: resProv,
      kategori: resKat,
      kuliner: sanitized,
      popular: sanitized.map((c: any) => c.id), // Semua data DB sementara masuk list populer
      baru: sanitized,
    };
  } catch (error) {
    console.error("Load All Master Data Gagal:", error);
    return { provKota: {}, kategori: [], kuliner: [], popular: [], baru: [] };
  }
}

/**
 * Fungsi bantuan untuk load file JSON statis.
 */
export async function loadJSON<T>(path: string): Promise<T> {
  const base = "http://localhost:3000";
  const url = path.startsWith("http") ? path : `${base}${path.startsWith("/") ? "" : "/"}${path}`;
  const res = await fetch(url, { cache: "no-store" });
  if (!res.ok) throw new Error(`Gagal memuat JSON: ${url}`);
  return res.json();
}